.container .row .col-lg-6 .font-weight-bold .violet{
    color: #8569CF;
}
.container .row .col-lg-6 .font-weight-light .violet{
    color: #8569CF;
}
.container .row .col-lg-6 h6{
    color:#65C8DB;
    font-size: 17.5px;
}
.container .row .col-lg-6 .row .col-lg-4 button, .container .row .col-lg-6 .row .col-lg-3 button{
    background: transparent;
    border-radius: 70px;
    color: #ffffff;
    font-size: 12px;
    height: 40px;
}
.container .row .col-lg-6 .row .col-lg-4 .btn-blue{
    background-color: #65C8DB;
    border: 2px solid #65C8DB;
    width: 175px;
}
.container .row .col-lg-6 .row .col-lg-4 .btn-blue img{
    background-color: #65C8DB !important;
    margin-left: 15px;
}
.container .row .col-lg-6 .row .col-lg-3 .btn-violet{
    background-color: #8569CF;
    border: 2px solid #8569CF;
    width: 150px;
}
.container .row .col-lg-5 .card .card-body form .row .col-3 .input-simulator{
    border: 1px solid #333333;
    border-radius: 70px;
    height: 25px;
    width: 120px;
}
.container .row .col-lg-5 .card .card-body form .row .col-6 .p-input{
    float: right;
    font-size: 14px;
}
.container .row .col-lg-5 .card .card-body form .btn-credit{
    background-color: #8569CF;
    border: 2px solid #8569CF;
    border-radius: 70px;
    color: #ffffff;
    font-size: 14px;
    height: 40px;
    width: 190px;

}
.container .row .col-lg-5 .card .card-body #hr-title{
    border: 0.5px solid #333333;
    width: 45px;
}